import { Metadata } from 'next';
import { getTranslations } from 'next-intl/server';
import TextDecoratorClient from './TextDecoratorClient';

export async function generateMetadata({ params }: { params: Promise<{ locale: string }> }): Promise<Metadata> {
    const { locale } = await params;
    const t = await getTranslations({ locale, namespace: 'Tools.text-decorator.meta' });

    return {
        title: t('title'),
        description: t('description'),
    };
}

export default async function TextDecoratorPage({ params }: { params: Promise<{ locale: string }> }) {
    const { locale } = await params;
    return <TextDecoratorClient />;
}
